<template>
	<div class="sb_page3">
		<div class="cont">
			<div class="find">
				<div style="margin-top: 15px;width: 40%;margin: 0 auto">
				  <el-input placeholder="请输入内容" v-model="findWord" class="input-with-select">
				    <el-button slot="append" icon="el-icon-search" @click="getData(findWord)"></el-button>
				  </el-input>
				  <div class="word_main">
				  	<ul>
				  		<li>推荐:</li>
				  		<li v-for="item in keys" class="item" @click="getData(item)">{{item}}</li>
				  	</ul>
				  </div>
				</div>
            </div>
            <div class="box-border-shadow">
            	<el-row :gutter="20">
				  <el-col :span="18">
				  	<div class="grid-content bg-purple">
				  		<div id="graphCharts" style="width: 100%;height: 800px;"></div>
					</div>
				  </el-col>
				  <el-col :span="6" style="border-left: 1px solid #ccc">
				  	<div class="grid-content bg-purple">
				  		<div style="height: 800px;overflow-y:auto; ">
				  			<div style="text-align: left;font-size: 16px;margin: 30px 0;color: #b80000">相关推荐：</div>
				  			<div style="text-align: left;">主体:</div>
				  			<ul class="keys key1">
				  				<li v-for="item in keys_zhuti" @click="getData(item)">{{item}}</li>
				  			</ul>
				  			<div style="text-align: left;">值:</div>
				  			<ul class="keys key2">
				  				<li v-for="item in keys_zhi" @click="getData(item)">{{item}}</li>
				  			</ul>
				  		</div>
				  	</div>
				  </el-col>
				</el-row>
            </div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import reqData from "./../../../reqData.js"
	import * as echarts from 'echarts'
	export default{
		name:'sb_page3',
		data(){
			return {
				cont:'',
				dataList:[],
				findWord:'',
				keys:['广东增值税电子普通发票','湖北增值税电子普通发票'],
				keys_zhuti:[],
				keys_zhi:[]
			}
		},
		components:{
		},
		methods:{
			unique(arr,links){
	   　　　var newArr = [arr[0].name];
	   		 var data=[arr[0]];
	   		 //去重复项
	  　　 	 for(var i=1;i<arr.length;i++){
	　　　　		if(newArr.indexOf(arr[i].name) == -1){
	          　　  newArr.push(arr[i].name);
	          	    data.push(arr[i]);
	    　　  	}
    		 }
    		 //区分值和主题
    		 for(var i=0;i<data.length;i++){
    		 	if(data[i].tag&&data[i].tag==='发票信息'){
    		 		data[i].itemStyle={
			             normal: {
			                 color: '#68bdf6',
			                 borderColor:'#5ca8db',
			                 borderWidth:2,
			             }
			         }
			         data[i].symbol='diamond'
    		 	}
    		 }
    		 console.log(data);
	         return data;
		  },
		  getData(word){
		  	let _this=this;
		  	_this.keys_zhuti=[];
		  	_this.keys_zhi=[];
		 	 console.log(word);
		 	 word=word?word:'空';
		 	 var graphCharts = echarts.init(document.getElementById('graphCharts'));
		 	 graphCharts.showLoading();
		   	 reqData
		        .req({
		          apiName: "Neo4jToIvnView",
		           data: {             
		             word:word
		           }
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
		            let nodes=data.nodes
					let links=data.links;
					console.log(nodes);
					nodes=this.unique(nodes);
					for (var i = 0; i < nodes.length; i++) {
						if(nodes[i].tag=="发票信息"){
							_this.keys_zhi.push(nodes[i].name)
						}else{
							_this.keys_zhuti.push(nodes[i].name)
						}
						
					}
					// links=this.linksFormat(links);
					graphCharts.hideLoading();
			    	graphCharts.setOption(this.graphOption(nodes,links)); 
		    	    $(window).resize(function(){
						graphCharts.resize({
							width:'auto',
							height:'auto'
						})
				    })

		          }
		        })
		        .catch(err => {
		          console.log(err);
		       });
		  },
		  graphOption(nodes,links){
	    	let option = {
			     title: {
			         text: ''
			     },
			     tooltip: {
			         formatter: function(x) {
			             return x.data.name;
			         }
			     },
			     series: [{
			         type: 'graph',
			         layout: 'force',
			         focusNodeAdjacency: true,
			         symbolSize: 48,
			         roam: true,
			         edgeSymbol: ['', 'arrow'],
			         // edgeSymbolSize: [80, 10],
			         hoverAnimation:true,
			         force: {
			         	initLayout:'circular',
			             repulsion: 600,
        				 layoutAnimation:false,
        				 gravity:0.5,
			             edgeLength: [16, 30],
			             layoutAnimation:true
			         },
			         draggable: true,
					 // symbol:'diamond',
			         itemStyle: {
			             normal: {
			                 color: '#ff756e',
			                 borderColor:'#e06760',
			                 borderWidth:2,
			             },

			         },
			         lineStyle: {
			             normal: {
			                 width: 1,
			                 color: '#000'

			             }
			         },
			         edgeLabel: {
			             normal: {
			                 show: true,
			                 textStyle: {fontSize: 14,color:'#000'},
			                 formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>5){
									name=name.substr(0,5)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0,5);
									// v.nameSub=name;
								}
			                     return name;
			                 }
			             }
			         },
			         label: {
			         	show:true,
			            formatter: function(x) {
		                 	let name=x.data.name;
				         	if(name.length>3){
								name=name.substr(0,3)+'...';
								// v.nameSub=name;
							}else{
								name=name.substr(0,3);
								// v.nameSub=name;
							}
		                     return '{a|'+name+'}';
		                },
		                rich: {
					        a: {
					            color: '#fff',
					        },
					    }
			         },
			        nodes: nodes, 
					links:links 
			    }]
			};
			return option;
		  }
		},
		mounted: function(){
			 this.getData('空');
		},
	}
</script>
<style scoped="">
	.cont{
		width: 90%; 
		margin: 20px auto;
		padding:40px;
	}
	.word_main{
		margin: 20 auto;
		overflow: hidden;
	}
	.word_main li{
		float: left;
		line-height: 40px;
		color: #a2a2a2;
	}
	.word_main li.item{
		margin: 0 8px;
		cursor: pointer;
	}
	.word_main li.item:hover{
		color: #b80000;
	}
	.keys{
		overflow: hidden;
	}
	.keys>li{
		float: left;
		display: block;
		background-color: #ccc;
		/*border: 1px solid #30333a;*/
		font-size: 15px;
		color: #30333a;
		padding: 4px 8px;
		border-radius: 5px;
		margin: 10px 5px;
		cursor: pointer;
	}
	.key2>li{
		background-color: #30333a;
		/*border: 1px solid #9195a0;*/
		color: #ccc;
	}
</style>


